package kmm.project.demo.domain.usecase

import kmm.project.demo.domain.base.usecase.ResultUseCase
import kmm.project.demo.domain.repository.AuthenticationRepository
import org.koin.core.component.KoinComponent
import org.koin.core.component.inject

/**
 * Use case for checking if a user is signed in.
 */
class IsUserSignedInUseCase: ResultUseCase<Boolean>, KoinComponent {

    private val authenticationRepository: AuthenticationRepository by inject()

    override suspend fun execute(): Boolean =
        authenticationRepository.isUserSignedIn()
}
