package io.djungle.ui.home

import androidx.lifecycle.viewModelScope
import io.djungle.ui.base.vieweffects.NavigateTo
import io.djungle.ui.base.viewmodel.BaseViewModel
import kmm.project.demo.domain.usecase.GetTodoUseCase
import kmm.project.demo.domain.usecase.IsUserSignedInUseCase
import kmm.project.demo.domain.usecase.SignOutUseCase
import kotlinx.coroutines.launch
import org.koin.core.component.inject

/**
 * The view model for the home screen.
 */
class HomeViewModel : BaseViewModel<HomeViewState, HomeViewIntent>() {

    private val getTodoUseCase: GetTodoUseCase by inject()
    private val isUserSignedInUseCase: IsUserSignedInUseCase by inject()
    private val signOutUseCase: SignOutUseCase by inject()

    init {
        viewState = HomeViewState()

        checkAuthentication()
    }

    private fun checkAuthentication() {
        viewModelScope.launch {
            if (!isUserSignedInUseCase.execute()) {
                postViewEffect(NavigateTo("login", clearBackStack = true))
            }
        }
    }

    fun testApi() {
        println("Calling TODO API")
        viewModelScope.launch {
            val todo = getTodoUseCase.execute(GetTodoUseCase.Params(id = "1"))
            println(todo)
        }
    }

    override fun handleViewIntent(viewIntent: HomeViewIntent) {
        when (viewIntent) {
            HomeViewIntent.SignOut -> {
                viewModelScope.launch {
                    runCatching {
                        signOutUseCase.execute()
                    }
                    postViewEffect(NavigateTo("login", clearBackStack = true))
                }
            }

            HomeViewIntent.ToggleShowContent -> viewState = viewState.copy(
                showContent = !viewState.showContent
            )
        }
    }
}
