package io.djungle.ui.base.viewmodel

import androidx.lifecycle.ViewModel
import androidx.lifecycle.viewModelScope
import kotlinx.coroutines.channels.Channel
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.StateFlow
import kotlinx.coroutines.flow.receiveAsFlow
import kotlinx.coroutines.launch
import org.koin.core.component.KoinComponent

/**
 * The base view model.
 */
abstract class BaseViewModel<VS : ViewState, VI : ViewIntent> : ViewModel(), KoinComponent {

    private lateinit var _viewState: VS

    private val _viewStateFlow by lazy { MutableStateFlow(viewState) }
    private val _viewEffectFlow: Channel<ViewEffect> = Channel(Channel.BUFFERED)

    /**
     * The flow emitting the new view states.
     */
    val viewStateFlow: StateFlow<VS>
        get() = _viewStateFlow

    /**
     * The flow containing the effects to be managed by a view.
     * The values emitted on this flow triggers an action to be performed by the view following a request from the view model.
     */
    val viewEffectFlow
        get() = _viewEffectFlow.receiveAsFlow()

    protected var viewState: VS
        get() = _viewState
        set(value) {
            _viewState = value
            _viewStateFlow.value = value
        }

    /**
     * Called to handle intents.
     *
     * @param viewIntent The intent to handle.
     */
    fun setViewIntent(viewIntent: VI) {
        handleViewIntent(viewIntent)
    }

    /**
     * Posts a [ViewEffect] to be handled by the view.
     *
     * @param viewEffect The view effect to post.
     */
    protected fun postViewEffect(viewEffect: ViewEffect) {
        viewModelScope.launch {
            _viewEffectFlow.send(viewEffect)
        }
    }

    protected abstract fun handleViewIntent(viewIntent: VI)
}
