package kmm.project.demo.domain.usecase

import kmm.project.demo.domain.base.usecase.ActionResultUseCase
import kmm.project.demo.domain.entity.TodoEntity
import kmm.project.demo.domain.repository.TodoRepository
import org.koin.core.component.KoinComponent
import org.koin.core.component.inject

/**
 * Use case for getting a Todo.
 */
class GetTodoUseCase : ActionResultUseCase<GetTodoUseCase.Params, TodoEntity>, KoinComponent {

    private val todoRepository: TodoRepository by inject()

    /**
     * The parameters for the use case.
     *
     * @property id the ID
     */
    data class Params(val id: String)

    override suspend fun execute(params: Params): TodoEntity =
        todoRepository.getTodo(params.id)
}
