package kmm.project.demo.data.repository

import kmm.project.demo.data.dto.Todo
import kmm.project.demo.data.source.interfaces.TodoDataSource
import kmm.project.demo.domain.base.mapper.Mapper
import kmm.project.demo.domain.entity.TodoEntity
import kmm.project.demo.domain.repository.TodoRepository
import org.koin.core.component.KoinComponent
import org.koin.core.component.inject

/**
 * Default implementation of [TodoRepository].
 */
class DefaultTodoRepository : TodoRepository, KoinComponent {

    private val todoDataSource: TodoDataSource by inject()
    private val todoToTodoEntityMapper: Mapper<Todo, TodoEntity> by inject()

    override suspend fun getTodo(id: String): TodoEntity =
        todoToTodoEntityMapper.map(todoDataSource.getTodoById(id))
}
