package io.djungle.ui.home

import io.djungle.ui.base.viewmodel.ViewIntent

/**
 * The view intent for the home screen.
 */
sealed class HomeViewIntent : ViewIntent {

    /**
     * The intent to toggle the show content state.
     */
    data object ToggleShowContent : HomeViewIntent()

    /**
     * The intent to logout.
     */
    data object SignOut : HomeViewIntent()
}
