package kmm.project.demo.data.source.implementation

import kmm.project.demo.data.api.TodoApi
import kmm.project.demo.data.dto.Todo
import kmm.project.demo.data.source.interfaces.TodoDataSource
import org.koin.core.component.KoinComponent
import org.koin.core.component.inject

/**
 * Remote data source for accessing Todo data.
 */
class RemoteTodoDataSource : TodoDataSource, KoinComponent {

    private val todoApi: TodoApi by inject()

    override suspend fun getTodoById(id: String): Todo {
        return todoApi.getTodo(id)
    }
}
