package io.djungle.ui.login

import androidx.lifecycle.viewModelScope
import io.djungle.ui.base.vieweffects.NavigateTo
import io.djungle.ui.base.viewmodel.BaseViewModel
import kmm.project.demo.domain.usecase.SignInUseCase
import kmm.project.demo.domain.usecase.SignUpUseCase
import kotlinx.coroutines.launch
import org.koin.core.component.KoinComponent
import org.koin.core.component.inject

/**
 * The view model for the login screen.
 */
class LoginViewModel : BaseViewModel<LoginViewState, LoginViewIntent>(), KoinComponent {

    private val signUpUseCase: SignUpUseCase by inject()
    private val signInUseCase: SignInUseCase by inject()

    init {
        viewState = LoginViewState()
    }

    override fun handleViewIntent(viewIntent: LoginViewIntent) {
        when (viewIntent) {
            is LoginViewIntent.UpdateEmailAddress ->
                viewState = viewState.copy(
                    email = viewIntent.email
                )

            is LoginViewIntent.UpdatePassword ->
                viewState = viewState.copy(
                    password = viewIntent.password
                )

            LoginViewIntent.SignUp -> viewModelScope.launch {
                runCatching {
                    signUpUseCase.execute(
                        SignUpUseCase.Params(
                            email = viewState.email,
                            password = viewState.password
                        )
                    )
                }.onSuccess { authenticated ->
                    if (authenticated) {
                        postViewEffect(NavigateTo("home", clearBackStack = true))
                    }
                }
            }

            LoginViewIntent.SignIn -> viewModelScope.launch {
                runCatching {
                    signInUseCase.execute(
                        SignInUseCase.Params(
                            email = viewState.email,
                            password = viewState.password
                        )
                    )
                }.onSuccess { authenticated ->
                    if (authenticated) {
                        postViewEffect(NavigateTo("home", clearBackStack = true))
                    }
                }
            }
        }
    }
}
