package io.djungle.ui.login

import androidx.compose.foundation.layout.Arrangement
import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.Row
import androidx.compose.foundation.layout.fillMaxSize
import androidx.compose.foundation.text.KeyboardOptions
import androidx.compose.material.Button
import androidx.compose.material.Text
import androidx.compose.material.TextField
import androidx.compose.runtime.Composable
import androidx.compose.runtime.collectAsState
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.text.input.ImeAction
import androidx.compose.ui.text.input.PasswordVisualTransformation
import androidx.compose.ui.unit.dp
import androidx.navigation.NavController
import io.djungle.ui.base.compose.ViewEffectHandler
import io.djungle.ui.base.vieweffects.NavigateTo
import io.djungle.ui.ktx.clearBackStack
import kotlinproject.composeapp.generated.resources.Res
import kotlinproject.composeapp.generated.resources.login_button_signin
import kotlinproject.composeapp.generated.resources.login_button_signup
import kotlinproject.composeapp.generated.resources.login_email_hint
import kotlinproject.composeapp.generated.resources.login_password_hint
import org.jetbrains.compose.resources.stringResource
import org.jetbrains.compose.ui.tooling.preview.Preview
import org.koin.compose.viewmodel.koinViewModel
import org.koin.core.annotation.KoinExperimentalAPI

/**
 * The login screen.
 */
@OptIn(KoinExperimentalAPI::class)
@Composable
fun LoginScreen(
    navController: NavController,
    viewModel: LoginViewModel = koinViewModel(),
) {
    ViewEffectHandler(viewEffectFlow = viewModel.viewEffectFlow) { viewEffect ->
        when (viewEffect) {
            is NavigateTo -> navController.navigate(viewEffect.route) {
                if (viewEffect.clearBackStack) {
                    clearBackStack(navController)
                }
            }
        }
    }
    val viewState = viewModel.viewStateFlow.collectAsState().value
    Column(
        modifier = Modifier.fillMaxSize(),
        horizontalAlignment = Alignment.CenterHorizontally,
        verticalArrangement = Arrangement.Center
    ) {
        EmailAndPasswordScreen(
            viewState,
            viewModel::setViewIntent
        )
    }
}

@Composable
private fun EmailAndPasswordScreen(
    viewState: LoginViewState,
    onViewIntent: (LoginViewIntent) -> Unit = {}
) {
    Column(
        horizontalAlignment = Alignment.CenterHorizontally,
        verticalArrangement = Arrangement.spacedBy(10.dp, Alignment.CenterVertically)
    ) {
        TextField(
            value = viewState.email,
            onValueChange = { onViewIntent(LoginViewIntent.UpdateEmailAddress(it)) },
            label = { Text(stringResource(Res.string.login_email_hint)) },
            singleLine = true,
            keyboardOptions = KeyboardOptions.Default.copy(imeAction = ImeAction.Next)
        )
        TextField(
            value = viewState.password,
            onValueChange = { onViewIntent(LoginViewIntent.UpdatePassword(it)) },
            label = { Text(stringResource(Res.string.login_password_hint)) },
            visualTransformation = PasswordVisualTransformation(),
            singleLine = true,
        )
        Row(
            modifier = Modifier.align(Alignment.CenterHorizontally),
            horizontalArrangement = Arrangement.spacedBy(10.dp)
        ) {
            Button(onClick = { onViewIntent(LoginViewIntent.SignUp) }) {
                Text(stringResource(Res.string.login_button_signup))
            }
            Button(onClick = { onViewIntent(LoginViewIntent.SignIn) }) {
                Text(stringResource(Res.string.login_button_signin))
            }
        }
    }
}

@Composable
@Preview
private fun EmailAndPasswordScreenPreview() {
    EmailAndPasswordScreen(LoginViewState())
}
