package io.djungle.ui

import androidx.compose.material.MaterialTheme
import androidx.compose.runtime.Composable
import androidx.navigation.compose.NavHost
import androidx.navigation.compose.composable
import androidx.navigation.compose.rememberNavController
import androidx.navigation.createGraph
import kmm.project.demo.data.injection.dataModules
import kmm.project.demo.domain.injection.domainModules
import io.djungle.ui.home.HomeScreen
import io.djungle.ui.injection.viewModelModule
import io.djungle.ui.login.LoginScreen
import org.jetbrains.compose.ui.tooling.preview.Preview
import org.koin.compose.KoinApplication

@Composable
@Preview
fun App() {
    KoinApplication(application = {
        modules(
            viewModelModule,
            dataModules,
            domainModules,
        )
    }) {
        val navController = rememberNavController()
        val navGraph = navController.createGraph(startDestination = "home") {
            composable("home") {
                HomeScreen(navController = navController)
            }
            composable("login") {
                LoginScreen(navController = navController)
            }
        }
        MaterialTheme {
            NavHost(navController = navController, graph = navGraph)
        }
    }
}
