package kmm.project.demo.data.repository

import kmm.project.demo.data.source.interfaces.AuthenticationDataSource
import kmm.project.demo.domain.repository.AuthenticationRepository
import org.koin.core.component.KoinComponent
import org.koin.core.component.inject

/**
 * The default implementation of [AuthenticationRepository].
 */
class DefaultAuthenticationRepository : AuthenticationRepository, KoinComponent {

    private val authenticationDataSource: AuthenticationDataSource by inject()

    override suspend fun signUp(email: String, password: String): Boolean =
        authenticationDataSource.signUp(email, password)

    override suspend fun signIn(email: String, password: String): Boolean =
        authenticationDataSource.signIn(email, password)

    override suspend fun signOut() =
        authenticationDataSource.signOut()

    override suspend fun isUserSignedIn(isAnonymousValid: Boolean): Boolean =
        authenticationDataSource.isUserSignedIn(isAnonymousValid)
}
