package kmm.project.demo.data.injection

import kmm.project.demo.data.source.implementation.FirebaseAuthenticationDataSource
import kmm.project.demo.data.source.implementation.RemoteTodoDataSource
import kmm.project.demo.data.source.interfaces.AuthenticationDataSource
import kmm.project.demo.data.source.interfaces.TodoDataSource
import org.koin.dsl.module

/**
 * Module for providing data source dependencies.
 */
internal val dataSourceModule = module {
    single<AuthenticationDataSource> { FirebaseAuthenticationDataSource() }
    single<TodoDataSource> { RemoteTodoDataSource() }
}
