package io.djungle.ui.login

import io.djungle.ui.base.viewmodel.ViewIntent

/**
 * The view intent for the login screen.
 */
sealed class LoginViewIntent : ViewIntent {

    /**
     * The intent to update the user's email address.
     *
     * @param email The user's email address.
     */
    data class UpdateEmailAddress(val email: String) : LoginViewIntent()

    /**
     * The intent to update the user's password.
     *
     * @param password The user's password.
     */
    data class UpdatePassword(val password: String) : LoginViewIntent()

    /**
     * The intent to sign-up.
     */
    data object SignUp : LoginViewIntent()

    /**
     * The intent to sign-in.
     */
    data object SignIn : LoginViewIntent()
}
