package kmm.project.demo.data.source.implementation

import dev.gitlive.firebase.Firebase
import dev.gitlive.firebase.auth.auth
import kmm.project.demo.data.source.interfaces.AuthenticationDataSource

/**
 * A data source for authentication using Firebase.
 */
class FirebaseAuthenticationDataSource : AuthenticationDataSource {

    override suspend fun signUp(email: String, password: String): Boolean {
        try {
            Firebase.auth.createUserWithEmailAndPassword(email, password)
        } catch (e: Exception) {
            return false
        }
        return true
    }

    override suspend fun signIn(email: String, password: String): Boolean {
        try {
            Firebase.auth.signInWithEmailAndPassword(email, password)
        } catch (e: Exception) {
            return false
        }
        return true
    }

    override suspend fun signOut() {
        Firebase.auth.signOut()
    }

    override suspend fun isUserSignedIn(isAnonymousValid: Boolean): Boolean =
        Firebase.auth.currentUser != null && (isAnonymousValid || !Firebase.auth.currentUser!!.isAnonymous)
}
