package io.djungle.ui.base.vieweffects

import io.djungle.ui.base.viewmodel.ViewEffect

/**
 * The view effect to navigate to a specific route.
 *
 * @param route The route to navigate to.
 * @param clearBackStack Whether to clean the back stack.
 */
data class NavigateTo(
    val route: String,
    val clearBackStack: Boolean = false,
) : ViewEffect
